0.565, 1);\n\n  --ease-in-out-quad: cubic-bezier(0.455, 0.03, 0.515, 0.955);\n}\n",":root {\n  --white: #fff;\n  --white-transparent: rgba(255, 255, 255, 0.9);\n  --black-transparent: rgba(0, 0, 0, 0.9);\n\n  --w-25: #F5FAFF;\n  --w-50: #EBF5FF;\n  --w-75: #D6EBFF;\n  --w-100: #C2E1FF;\n  --w-200: #99CEFF;\n  --w-300: #70BAFF;\n  --w-400: #47A6FF;\n  --w-500: #1F93FF;\n  --w-600: #1976CC;\n  --w-700: #135899;\n  --w-800: #0C3B66;\n  --w-900: #061D33;\n\n  --g-50: #E6F8E6;\n  --g-100: #C4EEC2;\n  --g-200: #9DE29A;\n  --g-300: #6FD86F;\n  --g-400: #44CE4B;\n  --g-500: #00C41D;\n  --g-600: #00B412;\n  --g-700: #00A200;\n  --g-800: #009000;\n  --g-900: #007000;\n\n  --y-50: #FEFDE8;\n  --y-100: #FDFCC4;\n  --y-200: #FCF68C;\n  --y-300: #F9E736;\n  --y-400: #F6D819;\n  --y-500: #E6C00C;\n  --y-600: #C69608;\n  --y-700: #9E6b0A;\n  --y-800: #835510;\n  --y-900: #6F4514;\n\n  --s-25: #F8FAFC;\n  --s-50: #F1F5F8;\n  --s-75: #EBF0F5;\n  --s-100: #E4EBF1;\n  --s-200: #C9D7E3;\n  --s-300: #AEC3D5;\n  --s-400: #93AFC8;\n  --s-500: #779BBB;\n  --s-600: #446888;\n  --s-700: #37546D;\n  --s-800: #293F51;\n  --s-900: #1B2836;\n\n  --b-50: #F7F7F7;\n  --b-100: #ECECED;\n  --b-200: #DDDDE0;\n  --b-300: #C6C7CA;\n  --b-400: #ABACAF;\n  --b-500: #96979C;\n  --b-600: #6E6F73;\n  --b-700: #5A5B5F;\n  --b-800: #3C3D40;\n  --b-900: #1B1C1F;\n\n  --r-50: #FFEBEE;\n  --r-100: #FFCCD1;\n  --r-200: #F69898;\n  --r-300: #EF6F6F;\n  --r-400: #F94B4A;\n  --r-500: #FF382D;\n  --r-600: #F02B2D;\n  --r-700: #DE1E27;\n  --r-800: #D11320;\n  --r-900: #C30011;\n\n  --v-50: #FFFFFF;\n  --v-100: #F5EBFF;\n  --v-200: #DDB8FF;\n  --v-300: #C585FF;\n  --v-400: #AC52FF;\n  --v-500: #941FFF;\n  --v-600: #7B00EB;\n  --v-700: #6000B8;\n  --v-800: #450085;\n  --v-900: #2B0052;\n\n  // Common color aliases\n  --color-woot: var(--w-500);\n\n  --color-heading: #1f2d3d;\n  --color-body: #3c4858;\n\n  --color-border: var(--s-75);\n  --color-border-light: var(--s-50);\n  --color-border-dark: var(--s-100);\n\n  --color-background: var(--s-50);\n  --color-background-light: var(--s-25);\n\n  // Social and inboxes brand colors\n  --color-facebook-brand: #3b5998;\n  --color-twitter-brand: #1da1f2;\n  --color-whatsapp-brand: #25d366;\n  --color-sms-twilio: #f42f46;\n  --color-cloud-generic: #18b7b0;\n\n  // Tech-debt\n  --color-medium-gray: #8492a6;\n}\n","// scss-lint:disable\n/* http://meyerweb.com/eric/tools/css/reset/\n   v2.0 | 20110126\n   License: none (public domain)\n*/\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed,\nfigure, figcaption, footer, header, hgroup,\nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font-size: 100%;\n  vertical-align: baseline;\n}\n\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure,\nfooter, header, hgroup, menu, nav, section {\n  display: block;\n}\n\nbody {\n  line-height: 1;\n}\n\nol, ul {\n  list-style: none;\n}\n\nblockquote, q {\n  quotes: none;\n}\n\nblockquote:before, blockquote:after,\nq:before, q:after {\n  content: none;\n}\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n","@tailwind base;\n","$button-border-width: 1px;\n// Buttons\n.button {\n  appearance: none;\n  background: $color-primary;\n  border: $button-border-width solid $color-primary;\n  border-radius: $border-radius;\n  color: $color-white;\n  cursor: pointer;\n  display: inline-block;\n  font-size: $font-size-default;\n  height: $space-two * 2;\n  line-height: $line-height;\n  outline: none;\n  padding: $space-smaller $space-normal;\n  text-align: center;\n  text-decoration: none;\n  transition: background .2s, border .2s, box-shadow .2s, color .2s;\n  user-select: none;\n  vertical-align: middle;\n  white-space: nowrap;\n\n  &:focus,\n  &:hover {\n    background: lighten($color-primary, 7%);\n    border-color: $color-primary;\n    text-decoration: none;\n  }\n\n  &:active,\n  &.active {\n    background: $color-primary;\n    border-color: darken($color-primary, 5%);\n    color: lighten($color-primary, 20%);\n    text-decoration: none;\n  }\n\n  &[disabled],\n  &:disabled,\n  &.disabled {\n    cursor: default;\n    opacity: .5;\n    pointer-events: none;\n  }\n\n  &.small {\n    font-size: $font-size-small;\n    height: $space-medium;\n    padding: $space-smaller $space-slab;\n  }\n\n  &.large {\n    font-size: $font-size-medium;\n    height: $space-larger;\n    padding: $space-small $space-medium;\n  }\n\n  &.block {\n    width: 100%;\n  }\n\n  &.transparent {\n    background: transparent;\n    border: 0;\n    height: auto;\n  }\n\n  &.compact {\n    padding: 0;\n  }\n}\n","// Font sizes\n$font-size-micro: 0.5rem;\n$font-size-mini: 0.625rem;\n$font-size-small: 0.75rem;\n$font-size-default: 0.875rem;\n$font-size-medium: 1rem;\n$font-size-large: 1.25rem;\n$font-size-big: 1.5rem;\n$font-size-bigger: 2rem;\n$font-size-mega: 2.5rem;\n$font-size-giga: 3.5rem;\n\n// spaces\n$zero: 0;\n$space-micro: 0.125rem;\n$space-smaller: 0.25rem;\n$space-small: 0.5rem;\n$space-one: 0.625rem;\n$space-slab: 0.75rem;\n$space-normal: 1rem;\n$space-two: 1.25rem;\n$space-medium: 1.5rem;\n$space-large: 2rem;\n$space-larger: 3rem;\n$space-big: 4rem;\n$space-jumbo: 5rem;\n$space-mega: 6.25rem;\n\n$border-radius-small: 0.1875rem;\n$border-radius-normal: 0.3125rem;\n\n// font-weight\n$font-weight-feather: 100;\n$font-weight-light: 300;\n$font-weight-normal: 400;\n$font-weight-medium: 500;\n$font-weight-bold: 600;\n$font-weight-black: 700;\n\n// Colors\n$color-woot: #1f93ff;\n$color-primary: $color-woot;\n$color-gray: #6e6f73;\n$color-light-gray: #999a9b;\n$color-border: #e0e6ed;\n$color-border-transparent: rgba(224, 230, 237, 0.5);\n$color-border-light: #f0f4f5;\n$color-border-dark: #cad0d4;\n$color-background: #f4f6fb;\n$color-background-light: #fafafa;\n$color-white: #fff;\n$color-body: #3c4858;\n$color-heading: #1f2d3d;\n$color-error: #ff382d;\n$color-success: #44ce4b;\n\n// Color-palettes\n\n$color-primary-light: #c7e3ff;\n$color-primary-dark: darken($color-woot, 20%);\n\n// Snackbar default\n$woot-snackbar-bg: #323232;\n$woot-snackbar-button: #ffeb3b;\n\n$swift-ease-out-duration: 0.4s !default;\n$swift-ease-out-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n$swift-ease-out: all $swift-ease-out-duration $swift-ease-out-timing-function !default;\n\n$border-radius: 0.1875px;\n$line-height: 1;\n$footer-height: 11.2rem;\n$header-expanded-height: $space-medium * 10;\n\n$font-family: 'Inter',\n  -apple-system,\n  system-ui,\n  BlinkMacSystemFont,\n  'Segoe UI',\n  Roboto,\n  'Helvetica Neue',\n  Tahoma,\n  Arial,\n  sans-serif;\n\n// Break points\n$break-point-medium: 667px;\n","// scss-lint:disable PropertySortOrder DeclarationOrder QualifyingElement\n$form-border-width: 1px;\n$input-height: $space-two * 2;\n\n.form-input {\n  @include placeholder {\n    color: $color-gray;\n  }\n\n  appearance: none;\n  background: $color-white;\n  border: 1px solid $color-border;\n  border-radius: $border-radius;\n  box-sizing: border-box;\n  color: $color-body;\n  display: block;\n  font-family: $font-family;\n  font-size: $font-size-medium;\n  height: $input-height;\n  line-height: 1.5;\n  max-width: 100%;\n  outline: none;\n  padding: $space-smaller;\n  position: relative;\n  transition: background .2s,\n  border .2s,\n  box-shadow .2s,\n  color .2s;\n  width: 100%;\n\n  &:focus {\n    border-color: $color-primary;\n  }\n\n  &::placeholder {\n    color: $color-gray;\n  }\n\n  // Input sizes\n  &.small {\n    font-size: $font-size-small;\n    height: $space-large;\n    padding: $space-small $space-one;\n  }\n\n  &.default {\n    font-size: $font-size-default;\n    height: $space-medium;\n    padding: $space-smaller $space-slab;\n  }\n\n  &.large {\n    font-size: $font-size-medium;\n    height: $space-larger;\n    padding: $space-slab $space-two;\n  }\n\n  &.input-inline {\n    display: inline-block;\n    vertical-align: middle;\n    width: auto;\n  }\n\n  // Input types\n  &[type=\"file\"] {\n    height: auto;\n  }\n}\n\n// Form element: Textarea\ntextarea.form-input {\n  font-family: $font-family;\n\n  @include placeholder {\n    color: $color-light-gray;\n  }\n\n  &,\n  &.large,\n  &.small {\n    height: auto;\n  }\n}\n","// scss-lint:disable PseudoElement SpaceBeforeBrace VendorPrefix\n$shadow-color-1: rgba(50, 50, 93, 0.08);\n$shadow-color-2: rgba(0, 0, 0, 0.07);\n$shadow-color-3: rgba(50, 50, 93, 0.08);\n$shadow-color-4: rgba(0, 0, 0, 0.05);\n\n$color-shadow-medium: rgba(50, 50, 93, 0.08);\n$color-shadow-light: rgba(50, 50, 93, 0.04);\n$color-shadow-large: rgba(50, 50, 93, 0.25);\n$color-shadow-outline: rgba(66, 153, 225, 0.5);\n\n@mixin normal-shadow {\n  box-shadow: 0 $space-small $space-normal $shadow-color-1,\n    0 $space-smaller $space-slab $shadow-color-2;\n}\n\n@mixin light-shadow {\n  box-shadow: 0 $space-smaller 6px $shadow-color-3, 0 1px 3px $shadow-color-4;\n}\n\n@mixin placeholder {\n  &::-webkit-input-placeholder {\n    @content;\n  }\n\n  &:-moz-placeholder {\n    @content;\n  }\n\n  &::-moz-placeholder {\n    @content;\n  }\n\n  &:-ms-input-placeholder {\n    @content;\n  }\n}\n\n@mixin shadow {\n  box-shadow: 0 1px 10px 2px $color-shadow-medium,\n    0 1px 5px 1px $color-shadow-light;\n}\n\n@mixin shadow-medium {\n  box-shadow: 0 4px 24px 4px $color-shadow-medium,\n    0 2px 16px 2px $color-shadow-light;\n}\n\n@mixin shadow-large {\n  box-shadow: 0 10px 15px -16px $color-shadow-medium,\n    0 4px 6px -8px $color-shadow-light;\n}\n\n@mixin shadow-big {\n  box-shadow: 0 20px 25px -20px $color-shadow-medium,\n    0 10px 10px -10px $color-shadow-light;\n}\n\n@mixin shadow-mega {\n  box-shadow: 0 25px 50px -12px $color-shadow-big;\n}\n\n@mixin shadow-inner {\n  box-shadow: inset 0 2px 4px 0 $color-shadow-light;\n}\n\n@mixin shadow-outline {\n  box-shadow: 0 0 0 3px $color-shadow-outline;\n}\n\n@mixin shadow-none {\n  box-shadow: none;\n}\n\n@mixin button-size {\n  min-height: $space-large;\n  min-width: $space-large;\n}\n",".icon-button {\n  @include button-size;\n}\n","@font-face {\n  font-family: 'Inter';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url('~shared/assets/fonts/Inter-Regular.woff2') format('woff2'),\n       url('~shared/assets/fonts/Inter-Regular.woff') format('woff');\n}\n\n@font-face {\n  font-family: 'Inter';\n  font-style: normal;\n  font-weight: 500;\n  font-display: swap;\n  src: url('~shared/assets/fonts/Inter-Medium.woff2') format('woff2'),\n       url('~shared/assets/fonts/Inter-Medium.woff') format('woff');\n}\n",".file-uploads .attachment-button + label {\n  cursor: pointer;\n}\n\n.conversation-wrap {\n  .agent-message {\n    align-items: flex-end;\n    display: flex;\n    flex-direction: row;\n    justify-content: flex-start;\n    margin: 0 0 $space-micro $space-small;\n    max-width: 88%;\n\n    .avatar-wrap {\n      flex-shrink: 0;\n      height: $space-medium;\n      width: $space-medium;\n\n      .user-thumbnail-box {\n        margin-top: -$space-large;\n      }\n    }\n\n    .message-wrap {\n      flex-grow: 1;\n      flex-shrink: 0;\n      margin-left: $space-small;\n      max-width: 90%;\n    }\n  }\n\n  &.is-typing .messages-wrap div:last-child {\n    .agent-message {\n      .agent-name {\n        display: none;\n      }\n\n      .user-thumbnail-box {\n        margin-top: 0;\n      }\n    }\n  }\n\n  .agent-name {\n    font-size: $font-size-small;\n    font-weight: $font-weight-medium;\n    margin: $space-small 0;\n    padding-left: $space-micro;\n  }\n\n  .has-attachment {\n    overflow: hidden;\n\n    :not([audio]) {\n      padding: 0;\n    }\n\n    &.has-text {\n      margin-top: $space-smaller;